html {
  height: 100vh;
  background-color: #ffecd1;
  color: #001524;
}

body #app {
  display: flex;
  flex-direction: column;

  align-items: center;
  justify-content: center;

  grid-gap: 6rem;
}

header h1 {
  display: flex;
  align-items: center;
}

header img {
  width: 10rem;
  height: 6rem;
}

section {
  display: flex;
  flex-direction: column;
  grid-gap: 2rem;
}

section #jumpscareImg {
  width: 20rem;
  height: 12rem;

  border-radius: 50%;
}

section #jumpscareImg:hover {
  animation-name: jumpscare;
  animation-duration: 2.5s;
  animation-timing-function: ease-out;
}

@keyframes jumpscare {
  from {
    transform: scale() rotate();
  }
  50% {
    transform: scale(10) rotate(-360deg);
  }
  to {
    transform: scale() rotate();
  }
}
